@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
* {
  font-family: 'Pangolin', cursive;
  margin: 0;
  padding: 0;
}
*:focus {
    outline: none;
}

.main {
  width: 100vw;
  height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #fbeee0;
}

.menu {
    width: 100vw;
    height: 60px;
    background: #222;
    color: floralwhite;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.sound_button {
    padding: 0;
    border: 0;
    background-color: #222;
    font-size: 15px;
    color: white;
    padding-left: 30px;
    cursor: pointer;
}

.sound_icon {
    margin-right: 10px;
}

.sound_on {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.sound_off {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: gray;
}

.lang_button {
    padding: 0;
    border: 0;
    background-color: #222;
    font-size: 15px;
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 30px;
    cursor: pointer;
}

.lang_icon {
    margin-left: 10px;
}

.pagination {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 30px;
}
  
.pagination button {
    background: 0;
    border: 0;
}

.pagination_item {
    height: 30px;
    width: 30px;
    border-radius: 100%;
    background-color: #222;
    margin-right: 2px;
    margin-left: 2px;
    cursor: pointer;
    border: 1px solid whitesmoke;
}
.pagination_item_active {
    background-color: whitesmoke;
}
.pagination_item:hover {
    background-color: whitesmoke;
}
.page {
    margin: 30px;
}

@media screen and (min-width: 500px) {
    .page {
        margin: 100px;
    }
}

@media screen and (min-width: 1000px) {
    .page {
        margin: 200px;
    }
}

@media screen and (min-width: 1500px) {
    .page {
        margin: 400px;
    }
}

.page h1 {
    margin-bottom: 20px;
    color: rgb(255, 128, 9);
}

.page p {
    margin-top: 10px;
    margin-bottom: 10px;
    color: #000000;
}

.page img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 100px;
    margin-top: 10px;
    margin-bottom: 10px;
}

@media screen and (min-width: 600px) {
    .page img {
        height: 200px;
    }
}

.button_start {
    background-color: #fbeee0;
    border: 2px solid #422800;
    border-radius: 30px;
    box-shadow: #422800 4px 4px 0 0;
    color: #422800;
    cursor: pointer;
    font-weight: 600;
    font-size: 18px;
    padding: 0 18px;
    line-height: 50px;
    text-align: center;
    text-decoration: none;
    -ms-user-select: none;
        user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.button_start:hover {
    background-color: rgb(251, 130, 109);
}

.button_start:active {
    box-shadow: #422800 2px 2px 0 0;
    transform: translate(2px, 2px);
}

@media (min-width: 768px) {
    .button_start {
        min-width: 120px;
        padding: 0 25px;
    }
}

.button_next {
    background-color: #fbeee0;
    border: 2px solid #422800;
    border-radius: 30px;

    color: #422800;
    cursor: pointer;
    font-weight: 600;
    font-size: 18px;
    padding: 0 18px;
    line-height: 50px;
    text-align: center;
    text-decoration: none;
    -ms-user-select: none;
        user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.button_next:hover {
    background-color: rgb(251, 130, 109);
}


.tripleBlock {
    display: flex;
    flex-direction: column;
    margin-right: 30px;
}

.tripleBlock_1{
    height: 35px;
    width: 105px;
    background-color: rgb(251, 130, 109);
}

.tripleBlock_2{
    height: 35px;
    width: 105px;
    background-color: rgb(109, 251, 152);
}

.tripleBlock_3{
    height: 35px;
    width: 105px;
    background-color: rgb(109, 170, 251);
}
